<template>
  <div
    class="dashboard-story"
    v-scroll-lock="isShow"
  >
    <PeriodModal
        v-if="showPeriodModal"
        :memorialId="memorial.id"
        :periods="periods"
        :defaultPeriod="period"
        @close="closePeriodModal"
        @selectPeriod="setPeriod"
    />

    <Tooltip
        v-if="isTooltipShow"
        :label="$ml.get(`dashboard.story.${type}.help.label`)"
        :withTalkLink="false"
        @close="isTooltipShow = false"
    >
      <div slot="content" class="tooltip__description">
        <span>
           {{ $ml.get(`dashboard.story.${type}.help.text`) }}
        </span>
      </div>
    </Tooltip>


    <div
      class="dashboard-story__modal"
      tabindex="0"
      @keydown.esc="close"
      ref="modal"
    >
      <div class="dashboard-story__header">
        <span class="icon icon__close dashboard-story__close" @click.prevent="close"></span>

        <div class="dashboard-story__avatar">
          <img v-if="isStoryFieldExist('owner') && null !== story.owner.user.profilePhoto && typeof story.owner.user.profilePhoto !== 'undefined' && story.owner.user.profilePhoto.status === 'ready' && story.owner.user.profilePhoto.url" :src="story.owner.user.profilePhoto.url" alt="avatar">
          <img v-else class="placeholder-avatar" src="~@/assets/img/icon/dashboard-avatar-placeholder.svg" alt="avatar">
        </div>

        <div class="dashboard-story__user">
          <span class="name">{{ isStoryFieldExist('owner')  ? story.owner.user.name : '' }}</span>
          <span class="relative">{{ isStoryFieldExist('owner')  ? story.owner.relationToMemorialPerson : '' }}</span>
        </div>
      </div>

      <div class="dashboard-story__content" ref="modalContent">
        <div class="dashboard-story__associating">
          <div class="associating__content">
            <div class="periods">
              <div class="label">
                <span class="text">{{ $ml.get('story.common.form.labels.associating') }}</span>
                <span class="icon icon__info ask-icon" @click="showTooltip('period')"></span>
              </div>

              <div class="dropdown-wrapper" @click.prevent.stop="openPeriodModal()">
                <div :class="['associating', 'memorial']">
                  <span class="icon icon__label-blue"></span>
                  <span class="text" v-html="period.name"></span>
                  <span class="icon caret"></span>
                </div>
              </div>
            </div>

            <div class="visible">
              <div class="label" v-if="'memorial' === this.$route.meta.type">
                <span class="text">{{ $ml.get('story.common.form.labels.visible') }}</span>
                <span class="icon icon__info ask-icon" @click="showTooltip('visible')"></span>
              </div>

              <div class="dropdown-wrapper" v-if="'memorial' === this.$route.meta.type">
                <div
                    class="memorial-sidebar__switcher-wrapper memorial-sidebar__switcher-wrapper--indent"
                    @click.prevent.stop="isStoryVisible = !isStoryVisible"
                >
                  <span v-if="isStoryVisible">{{ $ml.get('editMemorial.sidebar.relatives.editBlock.story.show') }}</span>
                  <span v-else>{{ $ml.get('editMemorial.sidebar.relatives.editBlock.story.hide') }}</span>

                  <div :class="['memorial-sidebar__switcher-box', {'switch': !isStoryVisible}]">
                    <div class="memorial-sidebar__switcher-point"></div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <span class="dashboard-story__label">{{ $ml.get('story.common.form.labels.content') }}</span>
        </div>

        <div class="dashboard-story__text">
          <div
              class="story-form__description dashboard"
              contentEditable="true"
              :value="description"
              v-html="story.description"
              @input="setDescription"
          ></div>

          <div class="story-form__media dashboard">
            <div class="upload__item" v-for="content in contents" :key="content.id">
              <span class="icon icon__close remove" @click.prevent="removeMedia(content.type, content.id)"></span>

              <div class="link__card" v-if="'link' === content.type && !checkIsYouTubeLink(content.link)">
                <img :src="content.previewImgLink" alt="">
                <div class="link__input">
                  <span class="icon icon__link link-icon"></span>
                  <input readonly type="text" :value="content.link">
                </div>
              </div>

              <div class="link__card" v-else-if="'link' === content.type && checkIsYouTubeLink(content.link)">
                <div class="upload__item">
                  <iframe
                      width="100%" height="300px"
                      :src="`https://www.youtube.com/embed/${getVideoId(content.link)}`"
                      frameborder="0"
                      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                      allowfullscreen
                  ></iframe>
                </div>
              </div>

              <img :src="content.file.url" v-else-if="null !== content.file &&'file' === content.type && 'image' === content.contentType" />

              <VideoPlayer
                  v-else-if="null !== content.file &&'file' === content.type && 'video' === content.contentType && 'ready' === content.file.status"
                  :options="{
                    autoplay: false,
                    controls: true,
                    sources: [
                      {
                        src: content.file.url,
                      }
                    ]
                  }"
              />

             <AudioPlayer
                 v-else-if="null !== content.file &&'file' === content.type && 'audio' === content.contentType && 'ready' === content.file.status"
                 :sources="[{
                   file: content.file.url,
                   type: '',
                 }]"
                 :classList="['create-story__audio']"
             />
            </div>
          </div>
        </div>
      </div>

      <div class="dashboard-story__footer">
        <Button
            :classList="['btn', 'btn__second', 'btn-draft']"
            @clicked="saveAsDraft"
            :label="'memorial' === this.$route.meta.type ? $ml.get('dashboard.story.controls.cancel') : $ml.get('dashboard.story.controls.draft')"
        ></Button>

        <Button
            v-if="'memorial' === this.$route.meta.type"
            :classList="['btn', 'btn__primary', 'btn-publish']"
            @clicked="save"
            :label="$ml.get('dashboard.story.controls.save')"
        ></Button>

        <Button
            v-else
            :classList="['btn', 'btn__primary', 'btn-publish']"
            @clicked="approve"
            :label="$ml.get('dashboard.story.controls.publish')"
        ></Button>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from "vuex";
  import Button from '@/components/Button';
  import AudioPlayer from '@/components/AudioPlayer';
  import VideoPlayer from '@/components/VideoPlayer';
  import PeriodModal from "@/components/PeriodModal";
  import { isYouTubeLink, parseVideoId } from '@/utils/YouTubeHelper';
  import Tooltip from "@/components/Tooltip";
  import { EventBus } from '@/utils/EventBus.js';

  export default {
    name: "Story",
    components: {
      Button,
      AudioPlayer,
      VideoPlayer,
      PeriodModal,
      Tooltip,
    },
    props: {
      storyId: {
        type: [String, Number],
        default: null,
      },
      editVersion: {
        type: [String, Number],
        default: null,
      },
      isVisible: {
        type: Boolean,
        default: false,
      },
    },
    data() {
      return {
        isTooltipShow: false,
        isShow: true,
        showPeriodModal: false,
        period: {},
        isStoryVisible: false,
        isOldStoryVisible: false,
        oldPeriodId: null,
        type: 'period',

        description: '',
        contents: [],
      };
    },
    computed: {
      ...mapGetters({
        story: 'story/story',
        memorial: 'dashboard/currentMemorial',
        periods: 'period/getPeriods',
      })
    },
    methods: {
      setDescription(event) {
        this.description = event.target.innerHTML.replace(/(<([^>]+)>)/gi, ' ');
        if ('<br>' === event.target.innerHTML.trim()) {
          event.target.innerHTML = '';
          this.description = '';
        }
      },
      removeMedia(type, id) {
        const idx = this.contents.findIndex(item => type === item.type || item.id === id);
        this.contents.splice(idx, 1);
      },
      showTooltip(type) {
        if('dashboard' === this.$route.meta.type && 'period' === type) {
          this.type = 'period1'
        } else {
          this.type = type;
        }

        this.isTooltipShow = true;
      },
      close() {
        this.isShow = false;
        this.$emit('close');
      },
      async save() {
        const files = this.contents.filter(item => {
          if('file' === item.type && null !== item.file) {
            return item;
          }
        });

        const links = this.contents.filter(item => {
          if('link' === item.type) {
            return item;
          }
        });

        const payload = {
          id: this.story.id,
          data: {
            editVersion: this.editVersion,
            memorialPeriodId: this.period.id,
            visible: this.isStoryVisible,
            description: this.description,
            files: files.map(item => {
              return {
                id: item.file.id,
                contentType: item.contentType,
                description: item.description,
              };
            }),
            links: links.map(item => {
              return {
                link: item.link,
                description: item.description,
              };
            }),
          },
        };

        if('dashboard' === this.$route.meta.type) {
          payload.data['approvalRequested'] = true;
        }

        const response = await this.$store.dispatch('story/update', payload);

        if(response.status) {
          if('memorial' === this.$route.meta.type) {
            await this.$store.dispatch('story/updateEditStories', response.data);
          } else {
            const data = {
              memorialId: this.memorial.id,
              data: response.data
            }
            await this.$store.dispatch('dashboard/updateStories', data);
          }

          this.close();
          return {
            status: true,
            data: response.data
          };
        } else {
         alert(response.data.response.data.errorDescription);
          return {
            status: false,
            data: response.data.response.data.errorDescription
          };
        }
      },
      async saveAsDraft() {
        const status = await this.save();
        if(status) {
          this.close();
        }
      },
      async approve() {
        const saveResponse = await this.save();

        if(saveResponse.status) {
          const approveResponse = await this.$store.dispatch('story/approve', {id: saveResponse.data.id, editVersion: saveResponse.data.editVersion});

          if(approveResponse.status) {
            this.isShow = false;
            EventBus.$emit('approve');
            return true;
          } else {
            alert(saveResponse.data.errorDescription);
            return false;
          }
        }
      },
      isStoryFieldExist(field) {
        return null !== this.story[field] && typeof this.story[field] !== 'undefined';
      },
      openPeriodModal() {
        this.showPeriodModal = true;
      },
      closePeriodModal() {
        this.showPeriodModal = false;
      },
      setPeriod(period) {
        this.period = period;
      },
      checkIsYouTubeLink(link) {
        return isYouTubeLink(link);
      },
      getVideoId(link) {
        return parseVideoId(link);
      },
    },
    async mounted() {
      await this.$store.dispatch('story/getStory', this.storyId);
      if('undefined' === typeof this.story || 0 === Object.keys(this.story).length) {
        this.close();
      }

      this.period = this.story.period;
      this.isStoryVisible = this.isVisible;

      await this.$store.dispatch('period/getPeriods',{id: this.memorial.id, withGeneral: true});

      if(this.$refs['modal']) {
        this.$refs['modal'].focus();
      }

      this.description = this.story.description;
      this.contents = Object.assign([], this.story.content);
    },
  }
</script>

<style scoped lang="scss">
  @import "~@/assets/scss/components/story-form.scss";
  @import "~@/assets/scss/components/dashboard-story.scss";
  @import "~@/assets/scss/pages/create-story.scss";
</style>