<template>
  <div class="dropdown" v-click-outside="onClickOutside">

    <div class="dropdown__btn" @click="isOpen = !isOpen">
      <slot name="dropdown__icon"></slot>
      <div class="dropdown__text">
        <slot name="dropdown__text">
          <div v-html="text"></div>
        </slot>
      </div>
      <span :class="['icon', 'caret', 'dropdown__caret', {'open': isOpen}]"></span>
    </div>

    <div class="dropdown__items" v-if="isOpen" v-click-outside="onClickOutside">
      <div class="dropdown__item" v-for="item in items" :key="item.value" @click.prevent="selectItem(item)">
        <div @click.prevent="selectItem(item)" class="dropdown__text">
          <img v-if="typeof item.options !== 'undefined' && typeof item.options.tag !== 'undefined' && 'img' === item.options.tag" :src="item.options.src">
          <span v-else v-html="item.label"></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Dropdown",
  props: {
    /**
     * items: [
     *  item: {
     *    label: '',
     *    value: '',
     *  }
     * ]
     */
    items: {
      type: Array,
      default() {
        return [];
      },
    },
    defaultText: {
      type: [String, Object],
      default: '',
    },
  },
  data() {
    return {
      isOpen: false,
      text: '',
    };
  },
  methods: {
    renderTextFromOptions(options) {
      if(typeof options.tag !== 'undefined') {
        switch (options.tag) {
          case "img":
            return `<img src="${options.src}" />`;
        }
      }
      return '';
    },
    selectItem(item) {
      if(typeof item.options !== 'undefined') {
        this.text = this.renderTextFromOptions(item.options);
      } else {
        this.text = item.label;
      }


      this.$emit('selected', item);
      this.isOpen = false;
    },
    onClickOutside() {
      this.isOpen = false;
    },
  },
  mounted() {
    if(typeof this.defaultText === 'object' && typeof this.defaultText.options !== 'undefined') {
      this.text = this.renderTextFromOptions(this.defaultText.options);
    } else {
      if(this.defaultText.length > 0) {
        this.text = this.defaultText;
      }
    }
  }
}
</script>

<style scoped lang="scss">
  @import "~@/assets/scss/components/dropdown.scss";
</style>