<template>
  <div class="dashboard">
    <Header
      :user="user"
      :classList="['dashboard-header']"
    />

    <div class="dashboard__container">

    <Story
      v-if="isShowStory"
      :storyId="showStoryId"
      :editVersion="showStoryEditVersion"
      @close="closeModal"
      @approve="approveStory"
    />

    <div class="dashboard__content">
      <div class="dashboard__user">
        <div class="dashboard__avatar">
          <img
            v-if="null !== user && null !== user.profilePhoto && typeof user.profilePhoto.url !== 'undefined'"
            :src="user.profilePhoto.url"
            alt="avatar"
          >
          <img
            v-else
            src="~@/assets/img/icon/dashboard-avatar-placeholder.svg"
            alt="avatar"
          >
        </div>
        <h1 class="dashboard__user-name">{{ $ml.with('name', null !== user ? user.name : '').get('dashboard.user.welcome') }}</h1>
      </div>

      <div class="dashboard__sites">
        <div class="dashboard__site">
          <div class="site__content">
            <div class="site__header">
              <span class="site__label">{{ $ml.get('dashboard.memorial.label') }}</span>
            </div>

            <div class="site__memorial">
              <div class="memorial__description">
                <div class="memorial__date">
                  <span class="memorial__label">{{ $ml.get('dashboard.memorial.createdAt') }}</span>
                  <span class="memorial__value">{{ $dateUtils.formatDate(memorial.createdAt, 'dd/MM/yyyy') }}</span>
                </div>
                <div class="memorial__subsription">
                  <span class="memorial__label">{{ $ml.get('dashboard.memorial.plan.label') }}</span>
                  <span class="memorial__value" v-if="memorial.paid && null !== memorial.paymentPlan && typeof memorial.paymentPlan !== 'undefined' && memorial.paymentPlan.type === 'monthly'">{{ $ml.with('amount', $ml.get('payments.monthly.price')).get('dashboard.memorial.plan.monthly') }}</span>
                  <span class="memorial__value" v-else-if="memorial.paid && null !== memorial.paymentPlan && typeof memorial.paymentPlan !== 'undefined' && memorial.paymentPlan.type === 'once'">{{ $ml.with('amount', $ml.get('payments.once.price')).get('dashboard.memorial.plan.once') }}</span>
                  <router-link tag="span" :to="{ name: 'memorials.payment', params: {slug: memorial.id}}" class="memorial__value underline" v-else>{{ $ml.get('dashboard.memorial.plan.choose') }}</router-link>
                </div>
              </div>

              <div class="memorial__control">
                <Button
                    :classList="['btn', 'btn__second', 'btn-edit']"
                    @clicked="goTo('memorials.edit', {id: memorial.id}, '_blank')"
                    :label="$ml.get('dashboard.memorial.control.edit')"
                />

                <Button
                    :classList="['btn', 'btn__second', 'btn-preview']"
                    @clicked="goTo('memorials.show', {slug: memorial.id}, '_blank')"
                    :label="$ml.get('dashboard.memorial.control.preview')"
                />

                <Button
                    :classList="['btn', 'btn__create', 'btn-add']"
                    @clicked="goTo('memorials.story.auth', {slug: memorial.id}, '_blank')"
                    :label="$ml.get('dashboard.memorial.control.add')"
                >
                  <span slot="icon-left" class="icon plus plus-blue"></span>
                </Button>
              </div>
            </div>

            <div class="site__stories">
              <div class="stories__info">
                <div class="stories__control">
                </div>

                <div class="stories__label">
                  <div class="title">
                    <span class="text">{{ $ml.get('dashboard.memorial.stories.label') }}</span>
                    <span class="icon icon__info ask-icon" @click="isTooltipShow = !isTooltipShow"></span>
                  </div>
                  <div class="description">
                    <span class="text">{{ $ml.get('dashboard.memorial.stories.description') }}</span>
                  </div>

                </div>

                <Button
                    :classList="['btn', 'btn__second', 'btn-add']"
                    @clicked="goTo('memorials.story.share', {slug: memorial.id}, '_blank')"
                    :label="$ml.get('dashboard.memorial.stories.btn.add')"
                >
                  <span slot="icon-left" class="icon icon__add-blue add-icon"></span>
                </Button>

              </div>

              <div :class="['stories__items', {'notfound': !isStoriesExist(memorial.id)}]">
                <div
                  class="stories__item stories__notfound"
                  v-if="!isStoriesExist(memorial.id)"
                >
                  <span class="text">{{ $ml.get('dashboard.memorial.stories.notfound') }}</span>
                </div>

                <div
                    v-else-if="stories[memorial.id].length > 0"
                    class="swiper-container swiper"
                    :id="$stringUtils.toCamelCase(`story-${memorial.slug}`)"
                >
                    <div class="swiper-wrapper">
                      <div
                          class="stories__item swiper-slide"
                          v-for="story in stories[memorial.id]"
                          :key="story.id"
                      >
                        <div class="stories__created">
                          <span class="label">{{ $ml.get('dashboard.memorial.story.created') }}</span>
                          <span class="date">{{ formatStoryDate(memorial.createdAt) }}</span>
                        </div>
                        <div class="stories__content">
                          <div class="info">
                            <div class="story__person">
                              <img class="person__photo" v-if="null !== story.owner && null !== story.owner.user.profilePhoto && typeof story.owner.user.profilePhoto.url !== 'undefined'" :src="story.owner.user.profilePhoto.url" alt="photo">
                              <img class="person__photo" v-else src="~@/assets/img/icon/relative-placeholder.svg" alt="photo">
                              <div class="person__info">
                                <span class="name">{{ story.owner.user.name }}</span>
                                <span class="company">{{ story.owner.relationToMemorialPerson }}</span>
                              </div>
                            </div>

                            <div class="approval">
                              <Button
                                  :classList="['btn', 'btn__primary', 'approval__btn']"
                                  @clicked="showStory(story)"
                                  :label=" $ml.get('dashboard.memorial.story.btn.approval')"
                              >
                              </Button>
                              <div class="approval__need"></div>
                            </div>
                          </div>

                          <div class="btn btn__label btn-relative">
                            <span class="icon icon__label-blue label-icon"></span>
                            <span class="period-name">{{ story.period.name }}</span>
                          </div>
                        </div>
                      </div>

                    </div>


                    <div :class="`swiper-button-prev swiper-button btn-prev-${memorial.slug}`" slot="button-prev">
                      <svg xmlns="http://www.w3.org/2000/svg" width="18.787" height="18.787"
                           viewBox="0 0 18.787 18.787">
                        <path id="Path_1969" data-name="Path 1969"
                              d="M22.787,12.219H8.5l6.564-6.564L13.393,4,4,13.393l9.393,9.393,1.656-1.656L8.5,14.568h14.29Z"
                              transform="translate(-4 -4)" fill="currentColor" />
                      </svg>
                    </div>
                    <div :class="`swiper-button-next swiper-button btn-next-${memorial.slug}`" slot="button-next">
                      <svg xmlns="http://www.w3.org/2000/svg" width="18.787" height="18.787"
                           viewBox="0 0 18.787 18.787">
                        <path id="Path_1969" data-name="Path 1969"
                              d="M18.787,8.219H4.5l6.564-6.564L9.393,0,0,9.393l9.393,9.393,1.656-1.656L4.5,10.568h14.29Z"
                              fill="currentColor" />
                      </svg>
                    </div>

                  </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    </div>
    <HelpCenter />

    <Tooltip
        v-if="isTooltipShow"
        :label="$ml.get('dashboard.help.label')"
        :withTalkLink="false"
        @close="isTooltipShow = false"
    >
      <div slot="content" class="tooltip__description">
        <span>
           {{ $ml.get('dashboard.help.text') }}
        </span>
      </div>
    </Tooltip>

    <Footer />
  </div>
</template>

<script>
  import Header from "@/views/memorials/edit/partials/Header";
  import Footer from "./partials/Footer";
  import Story from "./Story";
  import HelpCenter from '@/components/HelpCenter.vue';

  import Swiper, { Navigation } from 'swiper';

  Swiper.use([Navigation]);
  import 'swiper/swiper-bundle.css';

  import { mapGetters } from 'vuex';
  import Button from "@/components/Button";
  import Tooltip from "@/components/Tooltip";
  import { EventBus } from '@/utils/EventBus.js';

  export default {
    name: "Index",
    components: {
      Button,
      Header,
      Footer,
      HelpCenter,
      Story,
      Tooltip,
    },
    data() {
      return {
        isTooltipShow: false,
        swiperOptions: {
          slidesPerView: 'auto',
          navigation: {
            nextEl: null,
            prevEl: null,
          }
        },
        isShowStory: false,
        showStoryId: null,
        showStoryEditVersion: null,
        showContextMenu: false,
        slidersReady: [],

        slider: {},
      };
    },
    computed: {
      textDir() {
        return this.$ml.current === 'he';
      },
      typeNext() {
        return 'right';
      },
      typePrev() {
        return 'left';
      },
      ...mapGetters({
        memorial: 'dashboard/currentMemorial',
        stories: 'dashboard/stories',
        user: 'profile/getUser',
      }),
    },
    watch: {
      'memorial.slug'(slug) {
        this.sliderReInit();
      },
    },
    methods: {
      sliderReInit() {
        if(this.slider && Object.keys(this.slider).length) {
          this.slider.destroy(true, true);
        }

        this.initSlider();
      },
      initSlider() {
        let _this = this;
        setTimeout(() => {
          if(document.querySelector(`#${_this.$stringUtils.toCamelCase(`story-${_this.memorial.slug}`)}`)) {
            this.swiperOptions['navigation']['nextEl'] = `.swiper-button-next`;
            this.swiperOptions['navigation']['prevEl'] = `.swiper-button-prev`;

            this.slider = new Swiper(`#${_this.$stringUtils.toCamelCase(`story-${_this.memorial.slug}`)}`, _this.swiperOptions);
          }
        },1000);
      },
      closeModal() {
        this.isShowStory = false;
        this.showStoryId = null;
        this.showStoryEditVersion = null;
        this.$store.dispatch('story/saveStory', {});
      },
      async approveStory() {
        this.closeModal();
        await this.updateMemorial();

        this.sliderReInit();
      },
      hideContextMenu() {
        this.showContextMenu = false;
      },
      async updateMemorial() {
        await this.$store.dispatch('dashboard/setCurrentMemorial', this.memorial.id);
      },
      isStoriesExist(id) {
        return typeof this.stories[id] !== 'undefined' && this.stories[id].length > 0;
      },
      formatStoryDate(date) {
        if(this.$ml.current === 'en') {
          return this.$dateUtils.formatDate(date, 'MMMM dd, yyyy');
        }

        return this.$dateUtils.formatDate(date, 'dd MMMM yyyy', '-', this.$ml.current);
      },
      showStory(story) {
        this.isShowStory = true;
        this.showStoryId = story.id;
        this.showStoryEditVersion = story.editVersion;
      },
      goTo(route, params, target) {
        if('_blank' === target) {
          let routeData = this.$router.resolve({name: route, params: params});

          window.open(routeData.href, '_blank');
        } else {
          this.$router.push(route, params);
        }
      },
    },
    mounted() {
      let _this = this;
      setTimeout(() => {
        _this.initSlider();
        }, 2000);

      let bus = this.$ml.bus;
      bus.$on('vueml-language-changed', function () {
        if(typeof _this.slider !== 'undefined') {
          _this.sliderReInit();
        }
      });

      EventBus.$on('approve', () => {
        this.approveStory();
      });
    },
    async created() {
      // await this.$store.commit('dashboard/setCurrentMemorial', {});

      if(!this.$store.getters['auth/isLoggedIn'](['customer'])) {
       await this.$router.push({name: 'home'});
      }
    },
  }
</script>

<style scoped lang="scss">
  @import "~@/assets/scss/pages/dashboard.scss";
</style>