<template>
  <header :class="['edit-memorial__header', ...classList, {'open': isMenuShow}]"
          v-scroll-lock="isMobile && isMenuShow">
    <div class="memorial-header__main main-block">
      <div class="main-block__controls">
        <div class="management"  @click.prevent="isShowMemorialMenu = !isShowMemorialMenu">
          <img
              v-if="memorial && null !== memorial.profilePhoto && typeof memorial.profilePhoto !== 'undefined' && typeof memorial.profilePhoto.url !== 'undefined'"
              :src="memorial.profilePhoto.url"
              alt="avatar"
          >
          <img
              v-else
              src="~@/assets/img/icon/dashboard-placeholder.svg"
              alt="avatar"
          >

          <div class="management__user">
            <div class="name">
              <span>{{ memorial.name }}</span>
              <span class="icon caret"></span>
            </div>
            <div class="years"><span class="birth">{{ memorial.birthDate | getYear }}</span> <span class="separator">-</span> <span>{{ memorial.deathDate | getYear }}</span></div>
          </div>

          <div
              :class="['memorial-header__memorial-menu', {'empty': typeof memorials[0] === 'undefined'}]"
              v-click-outside="hideMemorialMenu"
              v-scroll-lock="isShowMemorialMenu"
              v-if="isShowMemorialMenu"
          >
              <div :class="['memorial-menu__items']" v-if="typeof memorials[0] !== 'undefined'">
                <div
                    :class="['memorial-menu__item', {'selected': memorialItem.id === memorial.id}]"
                    v-for="memorialItem in memorials"
                    :key="memorialItem.id"
                    @click="selectMemorial(memorialItem)"
                >
<!--                  <span class="icon icon__selected selected-icon" v-if="memorialItem.id === memorial.id"></span>-->
                  <img
                      v-if="null !== memorialItem.profilePhoto && typeof memorialItem.profilePhoto.url !== 'undefined'"
                      :src="memorialItem.profilePhoto.url"
                      alt="avatar"
                  >
                  <img v-else src="~@/assets/img/icon/dashboard-placeholder.svg" alt="">
                  <span>{{ memorialItem.name }}</span>
                </div>
              </div>

            <span class="separator"  v-if="typeof memorials[0] !== 'undefined'"></span>

            <div class="mobile-menu__item memorial-menu__item add" @click.prevent.stop="createMemorial">
              <span class="plus-icon"></span>
              <span class="memorial-add-btn">{{ $ml.get('editMemorial.menu.memorials.button') }}</span>
            </div>
          </div>
        </div>

        <div class="memorial" @click.prevent="isShowLinkMenu = !isShowLinkMenu">
          <span v-if="'memorial' === this.$route.meta.type">{{ $ml.get('editMemorial.header.memorial') }}</span>
          <span v-else>{{ $ml.get('editMemorial.header.dashboard') }}</span>

          <span class="icon caret"></span>

          <div
              class="context-menu memorial-header__menu-context"
              v-click-outside="hideLinkMenu"
              v-if="isShowLinkMenu"
          >
            <div class="context-menu__items">
              <div class="context-menu__item" @click="openMemorial">{{$ml.get('dashboard.header.menu.memorial') }}</div>
              <router-link tag="div" :to="{name: 'dashboard.show'}" class="context-menu__item">{{$ml.get('dashboard.header.menu.dashboard') }}</router-link>
            </div>
          </div>
        </div>
      </div>

      <div :class="['mobile-menu__hamburger', {'menu-active': isMenuShow}]" @click.stop.prevent="toggleMenu">
        <div :class="['hamburger', {'menu-active': isMenuShow}]">
          <span></span>
          <span></span>
        </div>
      </div>

      <router-link tag="div" class="main-block__logo" :to="{name: 'home'}">
        <img src="~@/assets/img/logo.svg" alt="Eternity">
      </router-link>

      <div class="main-block__profile">
        <router-link tag="div" :to="{name: 'dashboard.show'}" :class="['main-block__notification']">
          <span class="icon bell-icon"></span>
          <div class="count count__bg" v-if="countStories > 0">
            <span class="count__value">{{ countStories }}</span>
          </div>
        </router-link>

        <img
            v-if="null !== user && null !== user.profilePhoto && typeof user.profilePhoto.url !== 'undefined'"
            :src="user.profilePhoto.url"
            alt="avatar"
            @click="isShowAvatarMenu = !isShowAvatarMenu"
        >
        <img
            v-else
            src="~@/assets/img/icon/dashboard-avatar-placeholder.svg"
            alt="avatar"
            @click="isShowAvatarMenu = !isShowAvatarMenu"
        >

        <div
            class="context-menu memorial-header__avatar-menu"
            v-click-outside="hideAvatarMenu"
            v-if="isShowAvatarMenu"
        >
          <div class="context-menu__items">
            <router-link tag="div" :to="{name: 'dashboard.show'}" class="context-menu__item">{{ $ml.get('dashboard.header.menu.dashboard') }}
            </router-link>

            <div class="context-menu__item logout__label" @click="logout">
              <span class="icon icon__logout logout__icon"></span>
              {{ $ml.get('dashboard.header.menu.exit') }}
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="memorial-header__controls controls-block" v-if="!isMenuShow">
      <div class="controls-block__second">
        <Button
            :classList="['btn', 'btn-edit', {'btn__third': !isEditing}, {'btn__primary-2': isEditing}]"
            :label="$ml.get('editMemorial.header.edit')"
            @clicked="setEditingBlock"
        >
                    <span slot="icon-left"
                          :class="['icon', 'edit-icon', {'icon__edit-blue': !isEditing},  {'icon__edit': isEditing}, ]"></span>
        </Button>

        <div class="memorial-header__platforms">
          <Dropdown
              :defaultText="platform"
              :items="platforms"
              @selected="selectPlatform"
          />
        </div>

        <div class="memorial-header__languages">
          <Dropdown
              :defaultText="selectedLanguage.label"
              :items="languages"
              @selected="selectLanguage"
              :key="langChanged"
          >
            <span slot="dropdown__icon" class="icon icon__globe-blue"></span>
          </Dropdown>
        </div>

        <div :class="['memorial-header__stories', {'after': !memorial.paid}]">
          <span class="stories__label">{{ $ml.get('editMemorial.header.stories.label') }}</span>

          <RelativeList
              :classList="['stories__relatives']"
              :key="memorial.slug"
          />

          <Button
              :classList="['btn', 'btn__third', 'btn-story']"
              :label="$ml.get('editMemorial.header.stories.btn')"
              @clicked="goTo('memorials.story.share', {slug: memorial.id})"
          >
            <span slot="icon-left" class="icon icon__add-blue story-icon"></span>
          </Button>
        </div>

        <Button
            v-if="!memorial.paid"
            :classList="['btn', 'btn__third', 'btn-payment']"
            :label="$ml.get('editMemorial.header.payment')"
            @clicked="goTo('memorials.payment', {slug: memorial.id})"
        />
      </div>

      <div class="controls-block__main">
        <Button
            :classList="['btn', 'btn__second', 'btn-preview']"
            :label="$ml.get('editMemorial.header.preview')"
            @clicked="openPreview()"
        />

        <Button
            v-if="!this.memorial.public"
            :classList="['btn', 'btn__primary', 'btn-publish', {'disabled': !this.memorial || !this.memorial.paid}]"
            :label="$ml.get('editMemorial.header.publish')"
            @clicked="publishMemorial"
        />
      </div>
    </div>

    <div class="memorial-header__mobile-menu" v-if="isMenuShow">
      <div class="mobile-menu__block memorials">
        <span class="label">{{ $ml.get('editMemorial.menu.memorials.label') }}</span>
        <div class="mobile-menu__items">
          <div
              :class="['mobile-menu__item', {'selected': memorialItem.id === memorial.id}]"
              v-for="memorialItem in memorials"
              :key="memorialItem.id"
              @click="selectMemorial(memorialItem)"
          >
            <span class="icon icon__selected selected-icon" v-if="memorialItem.id === memorial.id"></span>
            <img
                v-if="null !== memorialItem.profilePhoto && typeof memorialItem.profilePhoto.url !== 'undefined'"
                :src="memorialItem.profilePhoto.url"
                alt="avatar"
            >
            <img v-else src="~@/assets/img/icon/dashboard-placeholder.svg" alt="">
            <span class="name">{{ memorialItem.name }}</span>
          </div>

          <div class="mobile-menu__item" @click.prevent.stop="createMemorial">
            <span class="plus-icon"></span>
            <span>{{ $ml.get('editMemorial.menu.memorials.button') }}</span>
          </div>
        </div>
      </div>

      <div class="mobile-menu__block navigation">
        <span class="label">{{ $ml.get('editMemorial.menu.navigation.label') }}</span>

        <div class="mobile-menu__items">
          <div :class="['mobile-menu__item', {'selected': 'dashboard' === $route.meta.type}]" @click.prevent.stop="openPage('dashboard')">
            <span class="icon icon__selected selected-icon" v-if="'dashboard' === $route.meta.type"></span>
            <span>{{ $ml.get('editMemorial.menu.navigation.dashboard') }}</span>
          </div>
          <div :class="['mobile-menu__item', {'selected': 'memorial' === $route.meta.type}]" @click.prevent.stop="openPage('memorial')">
            <span class="icon icon__selected selected-icon" v-if="'memorial' === $route.meta.type"></span>
            <span>{{ $ml.get('editMemorial.menu.navigation.memorial') }}</span>
          </div>
        </div>
      </div>

      <div class="mobile-menu__block relatives">
        <span class="label">{{ $ml.get('editMemorial.menu.relatives.label') }}</span>

        <div class="relatives__content">
          <RelativeList />

          <div class="relatives__share">
            <span class="label">{{ $ml.get('create.memorial.step2.form.title') }}</span>

            <div class="relatives-share__content">
              <span class="relatives-share__label">{{ $ml.get('create.memorial.step2.form.input.description') }}</span>

              <div class="relatives-share__text" @input="saveText" contenteditable="true">
                {{ form.text }}
              </div>

              <a class="relatives-share__memorial-link" ref="link">{{ form.link }}</a>

              <Button
                  :classList="['btn', 'btn__primary-2', 'relatives-share__copy-btn']"
                  :label="$ml.get('create.memorial.step2.form.buttons.copy')"
                  @clicked="copyLink"
              >
                <img slot="icon-left" src="~@/assets/img/icon/form-paper.svg" alt="">
              </Button>
            </div>

            <div class="relatives-share__links">
              <span>{{ $ml.get('create.memorial.step2.form.contacts.label') }}</span>
              <a
                  target="_blank"
                  :href="fbShareUrl"
                  class="relatives-share__link"
              >
                <img src="~@/assets/img/icon/facebook.svg" alt="">
              </a>
              <a
                  target="_blank"
                  class="relatives-share__link"
                  href="#"
                  @click.prevent="shareToMessenger()"
              >
                <img src="~@/assets/img/icon/facebook-messenger-1.svg" alt="">
              </a>
              <a
                  target="_blank"
                  :href="`whatsapp://send?text=${encodeURIComponent(shareText + '\n' + form.link)}`"
                  class="relatives-share__link"
                  data-action="share/whatsapp/share"
              >
                <img src="~@/assets/img/icon/whatsapp.svg" alt="">
              </a>
              <a
                  target="_blank"
                  :href="`mailto:?body=${encodeURIComponent(shareText + '\n' + form.link)}`"
                  class="relatives-share__link"
              >
                <img src="~@/assets/img/icon/envelope-blue.svg" alt="">
              </a>
            </div>
          </div>
        </div>
      </div>

      <div class="mobile-menu__block language">
        <span class="label">{{ $ml.get('editMemorial.menu.language.label') }}</span>

        <div class="mobile-menu__items">
          <div :class="['mobile-menu__item', {'selected': lang === $ml.current}]" v-for="lang in $ml.list" :key="lang"
               @click.prevent.stop="changeLang(lang)">
            <span class="icon icon__selected selected-icon" v-if="lang === $ml.current"></span>
            <span v-if="'he' === lang"></span>
            <span v-else>English</span>
          </div>
        </div>
      </div>

      <div class="mobile-menu__block logout">
        <div class="mobile-menu__items">
          <div :class="['mobile-menu__item']" @click.prevent.stop="logout">
            <span class="icon icon__logout logout__icon"></span>
            <span class="logout-label">{{ $ml.get('dashboard.header.menu.exit') }}</span>
          </div>
        </div>
      </div>

      <div class="memorial-header__bottom-nav">
        <div class="menu__title">{{ $ml.get('menu.contacts') }}</div>
        <div class="menu__conection">
          <div class="menu__conection-item">
            <a class="menu__relink" :href="$ml.get('contacts.email.url')">
              <img src="~@/assets/img/icon/menu-envelope.svg" alt="">
            </a>
            <span>{{ $ml.get('contacts.email.text') }}</span>
          </div>
          <div class="menu__conection-item">
            <a class="menu__relink" :href="$ml.get('contacts.phone.url')">
              <img src="~@/assets/img/icon/menu-phone.svg" alt="">
            </a>
            <span>{{ $ml.get('contacts.phone.text') }}</span>

          </div>
          <div class="menu__conection-item">
            <a class="menu__relink" :href="$ml.get('contacts.whatsapp.url')">
              <img src="~@/assets/img/icon/menu-whatsap.svg" alt="">
            </a>
            <span>{{ $ml.get('contacts.whatsapp.text') }}</span>
          </div>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import {EventBus} from '@/utils/EventBus';

import RelativeList from "@/components/RelativeList";
import Button from "@/components/Button";
import Dropdown from "@/components/Dropdown";
import {mapGetters} from "vuex";

export default {
  name: "Header",
  components: {
    Button,
    Dropdown,
    RelativeList,
  },
  props: {
    classList: {
      type: [Array, String],
      default: '',
    },
    user: {
      type: Object,
      default() {
        return {};
      },
    },
    previewRoute: {
      type: String,
      default() {
        return null;
      },
    },
  },
  data() {
    return {
      isEditing: false,
      isMenuShow: false,
      langChanged: false,
      isShowAvatarMenu: false,
      isShowLinkMenu: false,
      isShowMemorialMenu: false,

      platform: {
        value: 'desktop',
        label: '',
        options: {
          tag: 'img',
          src: require('@/assets/img/icon/desktop-blue.svg')
        },
      },

      selectedLanguage: {
        value: 'en',
        label: 'English',
      },

      languages: [
        {
          value: 'he',
          label: '',
        },
        {
          value: 'en',
          label: 'English',
        },
      ],

      platforms: [
        {
          value: 'desktop',
          label: '',
          options: {
            tag: 'img',
            src: require('@/assets/img/icon/desktop-blue.svg')
          },
        },
        {
          value: 'mobile',
          label: '',
          options: {
            tag: 'img',
            src: require('@/assets/img/icon/mobile-blue.svg')
          },
        },
      ],
      form: {
        text: '',
        link: '',
      },
    };
  },
  methods: {
    openMemorial() {
      let routeData = this.$router.resolve(  {name: 'memorials.edit', params: {id: this.memorial.id}});
      window.open(routeData.href, '_self');
    },
    openPreview() {
      if(this.previewRoute) {
        this.goTo(this.previewRoute, {slug: this.memorial.slug})
      }
      else {
        // this.goTo('memorials.show', {slug: this.memorial.id})

        let routeData = this.$router.resolve(  {name: 'memorials.show', params: {slug: this.memorial.id}});

        window.open(routeData.href, '_self');
      }
    },
    hideAvatarMenu() {
      this.isShowAvatarMenu = false;
    },
    hideLinkMenu() {
      this.isShowLinkMenu = false;
    },
    hideMemorialMenu() {
      this.isShowMemorialMenu = false;
    },
    selectPlatform(platform) {
      this.selectedPlatform = platform;

      this.$emit('changePlatform', this.selectedPlatform.value);
    },
    selectLanguage(language) {
      this.selectedLanguage = language;
      this.langChanged = !this.langChanged;

      const $editMemorial = document.getElementById('edit-memorial');
      if (null !== $editMemorial) {
        $editMemorial.setAttribute('dir', 'he' === this.selectedLanguage.value ? 'rtl' : 'ltr');
      }

      this.$emit('changeLanguage', this.selectedLanguage.value);
    },
    findLanguage() {
      this.selectedLanguage = this.languages.find(language => language.value === this.$ml.current);
      this.langChanged = true;
    },
    toggleMenu(event) {
      event.stopPropagation();

      this.isMenuShow = !this.isMenuShow;
    },
    setEditingBlock() {
      this.$emit('setEditingBlock', {block: 'default', isEdit: !this.isEditing});
    },
    publishMemorial() {
      if (this.memorial && this.memorial.paid) {
        const payload = {
          memorialId: this.memorial.id,
          editVersion: this.memorial.editVersion,
        };

        this.$store.dispatch('dashboard/publishMemorial', payload);
      }
    },
    goTo(route, params = null,) {
      this.$router.push({name: route, params});
    },
    logout() {
      this.$store.dispatch('auth/logout');
      this.$router.push({name: 'home'});
    },
    changeLang(lang) {
      this.$ml.change(lang);
      document.documentElement.setAttribute('lang', lang);
      document.documentElement.setAttribute('dir', 'he' === lang ? 'rtl' : 'ltr');
    },
    createMemorial() {
      this.$router.push({name: 'memorials.create.welcome'});
    },
    async selectMemorial(memorial) {
      await this.$store.dispatch('dashboard/setCurrentMemorial', memorial.id);
      if(this.$route.meta.type === 'memorial') {
        await this.$router.push({name: 'memorials.edit', params: {id: memorial.id}});
      }

      this.sortMemorials();

      //close menu
      // this.isMenuShow = false;
    },
    async openPage(type) {
      if(('memorial' === this.$route.meta.type && 'memorial' === type) || ('dashboard' === this.$route.meta.type && 'dashboard' === type)) {
        return;
      }

      if('memorial' === type) {
        await this.openMemorial();
      }

      if('dashboard' === type) {
        await this.$router.push({name: 'dashboard.show'});
      }
    },
    shareToMessenger() {
      let routeData = this.$router.resolve({name: 'memorials.story.share', params: {slug: this.memorial.slug}});

      let url = window.mobileCheck()
          ? `fb-messenger://share?link=${encodeURIComponent(this.form.link)}`
          : `https://www.facebook.com/dialog/send?app_id=${process.env.VUE_APP_FB_APP_ID}&display=popup&link=${encodeURIComponent(this.form.link)}&msg=${encodeURIComponent(this.shareText)}&redirect_uri=${encodeURIComponent(window.location.origin + routeData.href)}`

      window.open(url, '_blank');
    },
    saveText(event) {
      this.$store.dispatch('memorial/saveShareText', event.target.innerText);
    },
    copyLink() {
      navigator.clipboard.writeText(this.form.link);
    },
    sortMemorials() {
      this.$store.dispatch('dashboard/sortMemorialsByCurrent', this.memorial.id);
    },
  },
  computed: {
    isMobile() {
      return window.innerWidth < 768;
    },
    fbShareUrl() {
      let routeData = this.$router.resolve({name: 'memorials.story.share', params: {slug: this.memorial.slug}});

      return `https://www.facebook.com/dialog/share?app_id=${process.env.VUE_APP_FB_APP_ID}&display=popup&href=${encodeURIComponent(this.form.link)}&quote=${encodeURIComponent(this.shareText)}&redirect_uri=${encodeURIComponent(window.location.origin + routeData.href)}`;
    },
    ...mapGetters({
      memorials: 'dashboard/memorials',
      memorial: 'dashboard/currentMemorial',
      shareText: 'memorial/getShareText',
    }),
    countStories() {
      return this.$store.getters['dashboard/countStories'](this.memorial.id);
    },
  },
  watch: {
    'memorial.slug'(slug) {
      this.form.text = this.$ml.with('name', this.memorial.name).get('create.memorial.step2.form.input.text');
      this.$store.dispatch('story/saveShareText', this.form.text);

      this.form.link = `${window.location.origin}/memorials/${slug}/share`;
    },
  },
  mounted() {
    this.findLanguage();

    let _this = this;
    EventBus.$on('memorial-edit', (isEditing) => {
      _this.isEditing = isEditing;
    });

    //set share text
    this.form.link = `${window.location.origin}/memorials/${this.memorial.slug}/share`;

    if(!this.shareText) {
      this.form.text = this.$ml.with('name', this.memorial.name).get('create.memorial.step2.form.input.text');
      this.$store.dispatch('story/saveShareText', this.form.text);
    } else {
      this.form.text = this.shareText;
    }
  },
  async created() {
    await this.$store.dispatch('dashboard/resetMemorials');
    await this.$store.dispatch('dashboard/memorials');

    if(this.memorials.length === 0) {
      await this.$router.push({name: 'memorials.create.welcome'});
    }

    if(Object.keys(this.memorial).length === 0) {
      if(typeof this.memorials[0] !== 'undefined' && typeof this.memorials[0].id !== 'undefined') {
        await this.$store.dispatch('dashboard/setCurrentMemorial', this.memorials[0].id);
      }
    } else {
      await this.$store.dispatch('dashboard/setCurrentMemorial', this.memorial.id);
    }

    if(this.$route.meta.type === 'memorial' && this.$route.params.id !== this.memorial.id) {
      let memorial = this.memorials.find(memorial => memorial.id === this.$route.params.id);

      let memorialId = memorial.id;

      if(null === memorialId ||typeof memorialId === 'undefined') {
        memorialId = this.$route.params.id;
      }

      if(null !== memorialId && typeof memorialId !== 'undefined') {
        await this.$store.dispatch('dashboard/setCurrentMemorial', memorialId);
      }
    }

    if (!this.memorial) {
      await this.$router.push({name: 'home'});
    }

    this.sortMemorials();
  },
}
</script>

<style scoped lang="scss">
  @import "~@/assets/scss/pages/edit-memorial/header.scss";
</style>