<template>
  <div :class="['relative-list', ...classList]">
    <div class="relative-list__item" v-for="relative in relativeUsers.data">
      <img v-if="(typeof relative.profilePhoto !== 'undefined' && null !== relative.profilePhoto) && 'ready' === relative.profilePhoto.status && relative.profilePhoto.url.length > 0" :src="relative.profilePhoto.url" alt="relative">
      <img v-else src="~@/assets/img/icon/relative-placeholder.svg" alt="">
    </div>

    <div class="relative-list__item count" v-if="relativeUsers.count - 6 > 1">
      <span>{{ relativeUsers.count - 6 }}</span><span class="count-plus">+</span>
    </div>
  </div>
</template>

<script>
import {mapGetters} from "vuex";

export default {
  name: "RelativeList",
  props: {
    relatives: {
      type: [Array, Object],
      default() {
        return [];
      },
    },
    classList: {
      type: [Array, String],
      default() {
        return [];
      },
    },
  },
  computed: {
    ...mapGetters({
      memorial: 'dashboard/currentMemorial',
    }),
  },
  data() {
    return {
      relativeUsers: {},
    };
  },
  mounted() {
    let relatives = [];
    if(typeof this.memorial.relations !== 'undefined') {
      relatives = this.memorial.relations.map(relation => {
        relation.user['relationToMemorialPerson'] = relation.relationToMemorialPerson;
        return relation.user;
      });
    }

    this.relativeUsers = {
      count: relatives.length,
      data: relatives.splice(0, 6),
    };
  }
}
</script>

<style scoped lang="scss">
  @import "~@/assets/scss/components/relative-list.scss";
</style>>