<template>
  <div class="dashboard-footer">
    <div class="dashboard-footer__container">
      <div class="dashboard-footer__title-wrapper">
        <span class="dashboard-footer__logo"></span>
        <div class="dashboard-footer__text">
          {{ $ml.get('dashboard.footer.text') }}
        </div>
      </div>

      <div class="dashboard-footer__bottom">
        <div class="dashboard-footer__privacy">
          <a :href="`./files/${$ml.get(`footer.link3File`)}`"><img src="~@/assets/img/icon/footer-flag.svg" alt="">{{ $ml.get('dashboard.footer.privacy.links.abuse') }}</a>
          <a :href="`./files/${$ml.get(`footer.link2File`)}`" >{{ $ml.get('dashboard.footer.privacy.links.privacy') }}</a>
          <a :href="`./files/${$ml.get(`footer.link1File`)}`">{{ $ml.get('dashboard.footer.privacy.links.terms') }}</a>
        </div>

        <div class="dashboard-footer__control">
          <Button
              :classList="['btn', 'btn__third', 'dashboard-footer__btn', 'history']"
              @clicked="openLink($ml.get('dashboard.footer.controls.history.link'))"
              :label="$ml.get('dashboard.footer.controls.history.label')"
          />
          <Button

              :classList="['btn', 'btn__third', 'dashboard-footer__btn', 'you']"
              @clicked="openHelp"
              :label="$ml.get('dashboard.footer.controls.you.label')"
          />
        </div>


        <div class="dashboard-footer__rights">
          {{ $ml.with('year', new Date().getFullYear()).get('dashboard.footer.privacy.label') }}
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import Button from '@/components/Button';
import { EventBus } from '@/utils/EventBus.js';

export default {
  name: "Footer",
  components: {
    Button,
  },
  methods: {
    openLink(link) {
      if(link) {
        window.open(link, '_blank');
      }
    },
    openHelp() {
      EventBus.$emit('help-center-open');
    },
  },
}
</script>

<style scoped lang="scss">
  @import "~@/assets/scss/pages/dashboard/footer.scss";
</style>